.btn{
    @apply text-center font-bold rounded inline-block py-4 px-4 md:px-8;
}
.btn-sm{
    @apply text-sm py-2 px-4;
}
.btn-primary{
    @apply  bg-primary text-white hover:bg-purple-900 hover:shadow-inner;
 }
.btn-black{
    @apply  bg-black text-white hover:bg-primary  hover:shadow-inner;
 }
.btn-danger{
    @apply  bg-red-600 text-white hover:bg-red-700 hover:shadow-inner;
 }
.btn-outline-primary{
   @apply border border-primary text-primary hover:bg-primary hover:text-white;
}
.btn-outline-white{
    @apply border border-white text-white hover:bg-primary hover:text-white;
 }